.header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 75px;
  width: 100%;
  background: #85c17d;
  padding: 10px;
  box-sizing: border-box;

}

.header-title {
  font-size: 1.75em;
  font-weight: bold;
  margin: 10px 10px 0 10px;
  color: #18221e;
  text-align: left;
}
.header-links {
  display: flex;
  flex-direction: row;
  align-items: space-between;
  justify-content: center;
  height: 100%;
  width: 100%;
  margin-top: 20px;
}
.header-link {
  font-size: 1em;
  margin: 0 auto;
  color: #151c1d;
  text-decoration: none;
}
